#toast-container {
  margin-top: 1rem;
}

#toast-container.toast-top-full-width>.toast {
  max-width: 570px;
}

#toast-container>.toast-success {
  background-color: #35c8c3;
  opacity: 1;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

#toast-container>.toast-success:hover {
  background-color: #35c8c3;
  opacity: 1;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.4);
}

#toast-container>.toast-error {
  background-color: #eb5a46;
  opacity: 1;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

#toast-container>.toast-error:hover {
  background-color: #eb5a46;
  opacity: 1;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.4);
}

.moving-tab {
  display: none;
}

.btn-finish-reactWizard {
  color: #ffffff !important;
  background-color: #0a3cc4 !important;
  border-color: #0a3cc4 !important;
}

.btn-finish-reactWizard:hover {
  background-color: #0a3cc4e1 !important;
}

.btn-next-reactWizard {
  color: #ffffff !important;
  background-color: #3c0ac4 !important;
  border-color: #3c0ac4 !important;
}

.btn-prev-reactWizard {
  color: #ffffff !important;
  background-color: #c40a29 !important;
  border-color: #c40a29 !important;
}

.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
  color: #fff !important;
  background-color: #3c0ac4 !important;
}

.nav-pills .nav-link {
  cursor: pointer;
  color: #3c0ac4 !important;
}